@charset "UTF-8";
:root {
  --ermil-font: "Ermilovbold", sans-serif;
  --gothic-font: "DelaGothicOne", sans-serif;
  --black-color: #000;
  --white-color: #fff;
  --accent-color:#ff3944;
  --gray-color: #858585;
  --transparent-color: #d9d9d9;
  --content-width: 1370px;
  --container-offset: 15px;
  --container: calc(var(--content-width) + (var(--container-offset) * 2));
}

@font-face {
  font-family: "Ermilovbold";
  src: url("../fonts/Ermilovbold.woff2") format("woff2"), url("../fonts/Ermilovbold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "DelaGothicOne";
  src: url("../fonts/DelaGothicOne-Regular.woff2") format("woff2"), url("../fonts/DelaGothicOne-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
  box-sizing: inherit;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

body {
  margin: 0;
  min-width: 320px;
  font-family: var(--ermil-font);
  background-color: var(--color-white);
  font-weight: 400;
}

.container {
  max-width: var(--container);
  padding: 0 var(--container-offset);
  margin: 0 auto;
}

.container-fluid {
  max-width: 1920px;
  padding: 0 var(--container-offset);
  margin: 0 auto;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.btn-reset {
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.is-hidden {
  display: none !important;
}

.flex {
  display: flex;
}

.hero {
  padding-bottom: 25px;
  height: 871px;
  background-color: var(--black-color);
  /* Когда курсор на одной из карточек */
}
.hero__container {
  height: 100%;
  display: flex;
  padding: 0;
}
.hero__left {
  padding: 100px 140px;
  width: 30%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.hero__title {
  margin: 0;
  margin-bottom: 46px;
  font-family: var(--ermil-font);
  font-weight: 700;
  font-size: 47px;
  color: var(--white-color);
}
.hero__item:not(:last-child) {
  margin-bottom: 26px;
}
.hero__link {
  position: relative;
  font-family: var(--ermil-font);
  font-weight: 700;
  font-size: 27px;
  color: var(--white-color);
}
.hero__link:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: -5px;
  left: 0;
  background-color: var(--white-color);
  visibility: hidden;
  transform: scaleX(0);
  transition: all 0.3s ease-in-out 0s;
}
.hero__link:hover {
  transition: all 0.15s ease-out;
}
.hero__link:hover:before {
  visibility: visible;
  transform: scaleX(1);
}
.hero__rus {
  margin-right: 24px;
}
.hero__right {
  display: flex;
  max-width: 1415px;
  height: 103.5%;
  overflow-y: hidden;
  overflow-x: hidden;
}
.hero__strip {
  position: relative;
  cursor: pointer;
  flex: 20%;
  height: 100%;
  width: 284px;
  transition: flex 0.3s ease-in-out;
  background-position: center center;
  background-size: cover;
}
.hero__strip > * {
  margin: auto;
}
.hero__first {
  background-image: url(../images/hero/hero-img-1.png);
}
.hero__second {
  background-image: url(../images/hero/hero-img-2.png);
}
.hero__third {
  background-image: url(../images/hero/hero-img-3.png);
}
.hero__fourth {
  background-image: url(../images/hero/hero-img-4.png);
}
.hero__fifth {
  background-color: #b5b5b5;
}
.hero__year {
  position: absolute;
  top: 15%;
  left: 0px;
  font-family: var(--ermil-font);
  font-weight: 700;
  font-size: 96px;
  letter-spacing: 0.07em;
  color: var(--black-color);
  transform: rotate(-90deg);
  transition: all 0.5s ease;
}
.hero__strip:hover {
  transition: all 0.5s ease;
  flex: 60%;
}
.hero__strip:hover .hero__year {
  left: 25%;
}
.hero__strip:hover .hero__soon {
  top: 45%;
  left: 30%;
  transform: rotate(360deg);
}
.hero__select {
  position: absolute;
  bottom: 11px;
  left: 115px;
  width: 38px;
  height: 38px;
  background-color: var(--black-color);
  z-index: 5;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease-in-out;
}
.hero__soon {
  position: absolute;
  top: 65%;
  left: 10px;
  font-family: var(--ermil-font);
  font-weight: 700;
  font-size: 32px;
  color: var(--black-color);
  transform: rotate(-90deg);
  white-space: nowrap;
  transition: all 0.5s ease;
}
.hero__strip.strip--active .hero__select {
  opacity: 1;
  visibility: visible;
  transform: rotate(135deg);
}

.history {
  position: relative;
  margin-top: -25px;
}
.history__content {
  position: relative;
  display: flex;
  justify-content: center;
}
.history__logo {
  position: absolute;
  top: 18%;
  left: 3%;
  width: 65px;
  height: 65px;
}
.history__hidden {
  display: none;
}
.history__info {
  padding-top: 102px;
  padding-bottom: 102px;
  max-width: 718px;
}
.history__title {
  margin: 0;
  margin-bottom: 6px;
  font-family: var(--ermil-font);
  font-weight: 700;
  font-size: 47px;
  color: var(--white-color);
}
.history__year {
  margin-bottom: 20px;
  display: block;
  font-family: var(--ermil-font);
  font-weight: 700;
  font-size: 24px;
  color: var(--white-color);
}
.history__descr {
  margin: 0;
  margin-bottom: 70px;
  font-family: var(--ermil-font);
  font-weight: 700;
  font-size: 20px;
  line-height: 135%;
  text-transform: uppercase;
  color: var(--white-color);
}
.history__characters {
  font-family: var(--ermil-font);
  font-weight: 700;
  font-size: 20px;
  line-height: 145%;
  color: var(--white-color);
}

.news {
  position: relative;
  padding-top: 63px;
  height: 600px;
  overflow: hidden;
}
.news__video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.news__container {
  position: relative;
}
.news__title {
  margin: 0;
  font-family: var(--ermil-font);
  font-weight: 700;
  font-size: 128px;
  color: var(--white-color);
  text-transform: uppercase;
}
.news__block {
  position: absolute;
  top: 116.5px;
  left: 25.888px;
  padding: 32px 32px 76px 32px;
  max-width: 260px;
  background-color: var(--white-color);
}
.news__item:not(:last-child) {
  margin-bottom: 33px;
}
.news__author {
  margin-bottom: 10px;
  display: block;
  font-family: var(--ermil-font);
  font-weight: 700;
  font-size: 20px;
  color: var(--black-color);
  text-transform: uppercase;
}
.news .social__list .social__item:not(:last-child) {
  margin-right: 12px;
}

.statistics {
  padding: 67px 0px 64px 0px;
  background-color: var(--black-color);
}
.statistics__container {
  display: flex;
  flex-direction: column;
}
.statistics__title {
  margin: 0;
  margin-bottom: 41px;
  font-family: var(--ermil-font);
  font-weight: 700;
  font-size: 47px;
  color: var(--white-color);
  text-transform: uppercase;
}
.statistics__info {
  position: relative;
  align-self: center;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  column-gap: 121px;
}
.statistics__list {
  margin-bottom: 62px;
  display: flex;
  flex-direction: column;
}
.statistics__list li {
  display: flex;
}
.statistics__list li .item__name {
  font-family: var(--ermil-font);
  font-weight: 700;
  font-size: 20px;
  color: var(--white-color);
}
.statistics__list li .item__author {
  margin-right: auto;
  font-family: var(--ermil-font);
  font-weight: 700;
  font-size: 20px;
  color: var(--accent-color);
}
.statistics__list li .item__views {
  font-family: var(--ermil-font);
  font-weight: 700;
  font-size: 20px;
  color: var(--white-color);
  align-self: flex-end;
}
.statistics__list .statistics__item:not(:last-child) {
  margin-bottom: 45px;
}
.statistics__list .statistics__name {
  align-self: center;
}
.statistics__line {
  position: absolute;
  width: 226px;
  height: 2px;
  background-color: var(--white-color);
  transform: rotate(-90deg);
}
.statistics__line--1 {
  top: 115px;
  left: 175px;
}
.statistics__line--2 {
  top: 115px;
  left: 500px;
}
.statistics__line--3 {
  top: 115px;
  left: 845px;
}
.statistics__total {
  align-self: center;
  font-family: var(--ermil-font);
  font-weight: 700;
  font-size: 24px;
  color: var(--white-color);
}
.statistics__total span {
  color: var(--accent-color);
}

.announcements {
  padding: 120px 0;
}
.announcements__container {
  display: flex;
  flex-direction: column;
}
.announcements__title {
  margin: 0;
  margin-bottom: 36px;
  align-self: center;
  font-family: var(--ermil-font);
  font-weight: 700;
  font-size: 47px;
  color: var(--black-color);
  text-transform: uppercase;
}
.announcements__blocks {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 156px;
  row-gap: 24px;
}
.announcements__blocks .block-black {
  background-color: var(--black-color);
  color: var(--accent-color);
}
.announcements__blocks .block-gray {
  background-color: var(--gray-color);
  color: var(--white-color);
}
.announcements__blocks .block-white {
  background-color: var(--transparent-color);
  color: var(--white-color);
}
.announcements__block {
  position: relative;
  padding: 18px;
  margin-left: 60px;
  max-width: 500px;
  height: 80px;
}
.announcements__year {
  position: absolute;
  top: 28px;
  left: -85px;
  font-family: var(--ermil-font);
  font-weight: 700;
  font-size: 20px;
  color: var(--black-color);
}
.announcements__descr {
  margin: 0;
  font-family: var(--ermil-font);
  font-weight: 400;
  font-size: 15px;
}
.announcements__complete {
  left: -117px;
  text-transform: uppercase;
  transform: rotate(270deg);
}

.footer {
  padding: 125px 0;
  background-color: var(--black-color);
}
.footer__container {
  position: relative;
  max-width: 1113px;
  display: flex;
  justify-content: space-between;
}
.footer__info {
  margin-bottom: 13px;
  display: flex;
  align-items: center;
}
.footer__year {
  margin-right: 34px;
  font-family: var(--gothic-font);
  font-weight: 400;
  font-size: 47px;
  color: var(--accent-color);
  text-shadow: var(--accent-color) 1px 0 30px;
}
.footer__name {
  font-family: var(--gothic-font);
  font-weight: 400;
  font-size: 24px;
  color: var(--accent-color);
}
.footer__descr {
  margin: 0;
  max-width: 636px;
  font-family: var(--gothic-font);
  font-weight: 400;
  font-size: 15px;
  line-height: 100%;
  color: var(--accent-color);
}
.footer__copy {
  position: relative;
  width: 193px;
  font-family: var(--gothic-font);
  font-weight: 400;
  font-size: 41px;
  background-color: var(--accent-color);
  color: var(--black-color);
}
.footer__copy span {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 193px;
  height: 10px;
  background-color: var(--black-color);
}
.footer__line-bottom {
  position: absolute;
  bottom: -40px;
  width: 1113px;
  height: 2px;
  background-color: var(--accent-color);
}
.footer__line-delimiter {
  position: absolute;
  bottom: 50px;
  left: 53.5%;
  width: 180px;
  height: 2px;
  background-color: var(--accent-color);
  transform: rotate(-90deg);
}